.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3.75rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}
@media (min-width: 40em) {
  .nav {
    padding-top: 2.25rem;
    padding-bottom: 2.25rem;
  }
}
@media (min-width: 71.875em) {
  .nav {
    margin-bottom: 1.125rem;
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}
.nav__logo {
  display: inline-block;
}
.nav__logo img {
  width: 6.625rem;
  height: 1.125rem;
  transition: all 250ms ease-in;
}
@media (min-width: 40em) {
  .nav__logo img {
    width: 12.6875rem;
    height: 2rem;
  }
}
.nav__cta {
  display: inline-block;
  background-color: var(--white);
  color: var(--vy-dk-cyan);
  border-radius: 3.125rem;
  padding: 0.4375rem 1.4375rem;
  box-shadow: 0 0.125rem 0.625rem #d5d9dd;
  font-size: 0.6875rem;
  font-weight: 700;
  transition: opacity 250ms ease-in;
}
@media (min-width: 40em) {
  .nav__cta {
    padding: 0.95rem 4.2625rem;
    font-size: 0.8125rem;
  }
}
.nav__cta:hover {
  opacity: 0.7;
}